.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: 0.6s;
  padding: 30px 100px;
}

.logo {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60px;
  width: 60px;
  height: 70px;
  background-image: url(/kadajgaming/static/media/bannerphones.2024f229.png);
  position: relative;
  font-weight: 700;
  color: white;
  text-decoration: none;
  font-size: 2em;
  text-transform: uppercase;
  letter-spacing: 2px;
  -webkit-animation: fadeIn ease 2s;
          animation: fadeIn ease 2s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

.header ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: fadeInTop ease 2s;
          animation: fadeInTop ease 2s;
}

@-webkit-keyframes fadeInTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

.header ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: fadeInTop ease 2s;
          animation: fadeInTop ease 2s;
}

.header ul li {
  position: relative;
  list-style: none;
}

.header ul li a {
  position: relative;
  margin: 0 15px;
  text-decoration: none;
  color: white;
  letter-spacing: 2px;
  font-weight: 500px;
  transition: 0.6s;
  cursor: pointer;
}

.socials {
  margin: 0;
  font-size: 40px;
  align-items: center;
}

.sticky ul {
  display: flex;
  position: relative;
  list-style: none;
  margin: 0;
  align-items: center;
}

.sticky ul li {
  position: relative;
  list-style: none;
}

.sticky ul li a {
  position: relative;
  margin: 0 15px;
  text-decoration: none;
  color: white;
  letter-spacing: 2px;
  font-weight: 500px;
  transition: 0.6s;
  cursor: pointer;
}

.sticky {
  background-color: black;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: 0.6s;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  -webkit-animation: fadeInTop2 ease 1s;
          animation: fadeInTop2 ease 1s;
}

@-webkit-keyframes fadeInTop2 {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInTop2 {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

.bannerphones {
